<template>
    <div>
        <el-form ref="form" :model="form" label-width="110px" :rules="rules">
            <el-collapse v-model="collapseActive" @change="">
                <el-collapse-item title="　基本信息" name="1">

                    <el-form-item label="保险名称" prop="insurance_name">
                        <el-input v-model="form.insurance_name" :disabled="isDisabled(pageType)"></el-input>
                    </el-form-item>
                    <el-form-item label="保险种类" prop="dict_id">
                        <el-select v-model="form.dict_id" placeholder="请选择" class="mr10" :disabled="isDisabled(pageType)">
                            <el-option
                                v-for="item in insuranceTypeOptions"
                                :key="item.id"
                                :label="item.type_item_name"
                                :value="item.id">
                            </el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="保险公司" prop="company_id">
                        <el-select v-model="form.company_id" placeholder="请选择" class="mr10" :disabled="isDisabled(pageType)">
                            <el-option
                                v-for="item in insuranceCompanyOptions"
                                :key="item.company_id"
                                :label="item.company_name"
                                :value="item.company_id">
                            </el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="保险价格" prop="insurance_price">
                        <el-input v-model="form.insurance_price" :disabled="isDisabled()"></el-input>
                    </el-form-item>
                    <el-form-item label="保险金额" prop="insurance_amount">
                        <el-input v-model="form.insurance_amount" :disabled="isDisabled()"></el-input>
                    </el-form-item>

                </el-collapse-item>

            </el-collapse>
            <el-form-item class="form-submit">
                <el-button type="primary" @click="handleSubmit" v-if="pageType != 'view'">保存</el-button>
                <el-button @click="$router.back()">返回</el-button>
            </el-form-item>
        </el-form>
        
    </div>
</template>

<script>
    import * as API from '@/api/resourceManagement'
    export default {
        name: 'baseform',
        data: function(){
            return {
                collapseActive: ['1'],
                collapseAuditActive: ['1'],
                pageType: 'add',
                insuranceTypeOptions: [],
                insuranceCompanyOptions: [],
                form: {
                    company_id: "",
                    create_time: "",
                    dict_id: "",
                    insurance_amount: "",
                    insurance_name: "",
                    insurance_price: "",
                    status: ""
                },
                rules: {
                    insurance_name: [
                        { required: true, message: '请输入保险名称', trigger: 'blur' }
                    ],
                    dict_id: [
                        { required: true, message: '请选择保险种类', trigger: 'blur' }
                    ],
                    company_id: [
                        { required: true, message: '请选择保险公司', trigger: 'blur' }
                    ],
                    insurance_price: [
                        { required: true, message: '请输入保险价格', trigger: 'blur' }
                    ],
                    insurance_amount: [
                        { required: true, message: '请输入保险金额', trigger: 'blur' }
                    ],
                }
            }
        },
        mounted(){
            this.checkPath()
            this.getInsuranceType()
            this.getInsuranceCompany()
        },
        methods: {
            // 是否禁止输入
            isDisabled (type) {

                if(type == 'add'){
                    return this.pageType == type ? false : true;
                }else{
                    return this.pageType == 'view' ? true : false;
                }
            },
            checkPath() {
                let _path = this.$route.path,
                    _query = this.$route.query;
                if(_path.indexOf('add') > -1) {
                    this.pageType = 'add';
                }else if(_path.indexOf('edit') > -1) {
                    this.getFormData();
                    this.pageType = 'edit';
                }else if(_path.indexOf('view') > -1) {
                    this.pageType = 'view';
                    this.getFormData();
                }
            },
            getFormData(){
                API.apiProductSupplierView({id: this.$route.query.id, operation_uniqid: this.$route.query.operation_uniqid}).then(res => {
                    Object.assign(this.form, res.data)
                })
            },
            getInsuranceType(){
                API.apiProductSupplierType({}).then(res => {
                    this.insuranceTypeOptions = res.data
                })
            },
            getInsuranceCompany(){
                API.apiProductSupplierCompany({}).then(res => {
                    this.insuranceCompanyOptions = res.data
                })
            },
            handleSubmit() {
                this.$refs.form.validate((valid)=>{
                    if(valid){
                        if(this.pageType == 'add'){
                            API.apiProductSupplierAdd(this.form).then(res => {
                                this.$message.success('提交成功！');
                                this.$router.back()
                            })
                        }else{
                            API.apiProductSupplierEdit(this.form).then(res => {
                                this.$message.success('提交成功！');
                                this.$router.back()
                            })
                        }
                    }else{
                        return false
                    }
                })
                
                
                
            }
        }
    }
</script>

<style lang="scss" scoped>
    
</style>